.login{
    width: 100vw;
    height: 100vh;
    background-color: #d4d9e7;
    display: flex;
    align-items: center;
    justify-content: center;

    .loginWrapper{
        width: 80%;
        display: flex;

        .loginLeft{
            display: flex;
            flex-direction: column;
            flex: 1;
            margin: 0 20px;
            justify-content: center;

            .loginLogo{
                font-size: 60px;
                font-weight: 800;
                color: #5f805e;
                margin-bottom: 10px;
            }

            .loginDesc{
                font-size: 28px;
            }
        }

        .loginRight{
            display: flex;
            flex-direction: column;
            flex: 1;
            margin: 0 20px;
            justify-content: center;

            .bottomBox{
                height: 350px;
                padding: 30px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                border-radius: 10px;
                background-color: white;

                .loginInput{
                    height: 50px;
                    border-radius: 10px;
                    padding-left: 20px;
                    font-size: 18px;
                    border: 1px solid rgb(128, 128, 128);

                    &:focus{
                        outline: none;
                    }
                }

                .loginButton{
                    height: 50px;
                    width: 100%;
                    background-color: #011631;
                    color: white;
                    font-weight: 500;
                    font-size: 23px;
                    border-radius: 10px;
                    border: none;
                    margin-bottom: 5px;
                    cursor: pointer;
                }

                .loginRegisterButton{
                    height: 50px;
                    width: 100%;
                    background-color: #42b72a;
                    color: white;
                    font-weight: 500;
                    font-size: 23px;
                    border-radius: 10px;
                    border: none;
                    margin-bottom: 5px;
                    cursor: pointer;
                }

            }



        }
    }
}